.Frame {

  color: #000;
  font-family: HelveticaNeue, 'Helvetica Neue', 'Lucida Grande', Arial, sans-serif;
  /* width: 100%;
  height: 100%;
  position: absolute; */
    position: absolute;
}

/* window BEGIN */
.hidden {
  display: none;
}

.Frame span {
  line-height: 8px;
  vertical-align: 50%;
}

.Frame .window {
  background: #fff;

  /* margin: auto; */
  /* margin-top: 12.5vh; */
  border: 1px solid #acacac;
  border-radius: 6px;
  box-shadow: 0px 0px 20px #6c6c6c;

  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;

  height: 100%;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
}

.Frame .titlebar {
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0, #ebebeb, color-stop(1, #d5d5d5)));
  background: -webkit-linear-gradient(top, #ebebeb, #d5d5d5);
  background: -moz-linear-gradient(top, #ebebeb, #d5d5d5);
  background: -ms-linear-gradient(top, #ebebeb, #d5d5d5);
  background: -o-linear-gradient(top, #ebebeb, #d5d5d5);
  background: linear-gradient(to bottom, #ebebeb 0%, #d5d5d5 100%);
  color: #4d494d;
  font-size: 11pt;
  line-height: 20px;
  text-align: center;
  /* width: 100%; */
  height: 20px;
  border-top: 1px solid #f3f1f3;
  border-bottom: 1px solid #b1aeb1;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  cursor: default;

  border-top-left-radius: 4px;
  border-top-right-radius: 4px;

  z-index:200;
}

.Frame .buttons {
  padding-left: 8px;
  padding-top: 3px;
  float: left;
  line-height: 0px;
  position: absolute;
}

.Frame .buttons:hover div {
  visibility: visible;
}

.Frame .close {
  background: #ff5c5c;
  font-size: 9pt;
  width: 11px;
  height: 11px;
  border: 1px solid #e33e41;
  border-radius: 50%;
  display: inline-block;
}

.Frame .close:active {
  background: #c14645;
  border: 1px solid #b03537;
}

.Frame .close:active .closebutton {
  color: #4e0002;
}

.Frame .closebutton {
  color: #820005;
  visibility: hidden;
  cursor: default;
}

.Frame .minimize {
  background: #ffbd4c;
  font-size: 9pt;
  line-height: 11px;
  margin-left: 4px;
  width: 11px;
  height: 11px;
  border: 1px solid #e09e3e;
  border-radius: 50%;
  display: inline-block;
}

.Frame .minimize:active {
  background: #c08e38;
  border: 1px solid #af7c33;
}

.Frame .minimize:active .minimizebutton {
  color: #5a2607;
}

.Frame .minimizebutton {
  color: #9a5518;
  visibility: hidden;
  cursor: default;
}

.minimzed .content {
  display: none;
}

.minimized .window {
  height: 22px !important;

}

.Frame .zoom {
  background: #00ca56;
  font-size: 9pt;
  line-height: 11px;
  margin-left: 6px;
  width: 11px;
  height: 11px;
  border: 1px solid #14ae46;
  border-radius: 50%;
  display: inline-block;
}

.Frame .zoom:active {
  background: #029740;
  border: 1px solid #128435;
}

.Frame .zoom:active .zoombutton {
  color: #003107;
}

.Frame .zoombutton {
  color: #006519;
  visibility: hidden;
  cursor: default;
}

.Frame .content {
  padding: 0px;
  margin-bottom: -3px;

}

.Frame .content video {

    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
}
